@import url(https://fonts.googleapis.com/css?family=Orbitron);
body {
  margin: 0;
  width: 100%;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Orbitron',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.abbr {
  text-decoration: none;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.animationRight {
    -webkit-animation: moveRight 4s forwards;
            animation: moveRight 4s forwards;
    width: 120px;
    height: 120px;
    position: absolute;
    left: 10px;
}
.animationLeft {
    -webkit-animation: moveLeft 4s forwards;
            animation: moveLeft 4s forwards;
    width: 120px;
    height: 120px;
    position: absolute;
    right: 30px;
}

.background {
    width: 100%;
    height: 100%;
    background-color: transparent;
}
.animationFadeOut {
    -webkit-animation: fadeOut 2s forwards;
            animation: fadeOut 2s forwards;
    width: 100%;
    height: 100%;
}
.animationFadeIn {
    -webkit-animation: fadeIn 2.5s forwards;
            animation: fadeIn 2.5s forwards;
    width: 100%;
    height: 100%;

}
.animationBeBlack {
    background-color: black;
    width: 100%;
    height: 100%;
}

@-webkit-keyframes moveRight {
0% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px)
}
100% {
    -webkit-transform: translateX(1340px);
            transform: translateX(1340px)
}
}

@keyframes moveRight {
0% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px)
}
100% {
    -webkit-transform: translateX(1340px);
            transform: translateX(1340px)
}
}

@-webkit-keyframes moveLeft {
    0% {
        -webkit-transform: translateX(0px);
                transform: translateX(0px)
    }
    100% {
        -webkit-transform: translateX(-1360px);
                transform: translateX(-1360px)
    }
}

@keyframes moveLeft {
    0% {
        -webkit-transform: translateX(0px);
                transform: translateX(0px)
    }
    100% {
        -webkit-transform: translateX(-1360px);
                transform: translateX(-1360px)
    }
}

@-webkit-keyframes fadeIn {
    0% {
        background-color: black;
    }
    100% {
        background-color: transparent;
    }
}

@keyframes fadeIn {
    0% {
        background-color: black;
    }
    100% {
        background-color: transparent;
    }
}

@-webkit-keyframes fadeOut {
    0% {
        background-color: transparent;
    }
    100% {
        background-color: black;
    }
}

@keyframes fadeOut {
    0% {
        background-color: transparent;
    }
    100% {
        background-color: black;
    }
}
.react-calendar {
  width: 100%;
  max-width: 100%;
  height: 100%;
  background: transparent;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.125em;
}
.react-calendar--doubleView {
  width: 700px;
}
.react-calendar--doubleView .react-calendar__viewContainer {
  display: flex;
  margin: -0.5em;
}
.react-calendar--doubleView .react-calendar__viewContainer > * {
  width: 50%;
  margin: 0.5em;
}
.react-calendar,
.react-calendar *,
.react-calendar *:before,
.react-calendar *:after {
  box-sizing: border-box;
}
.react-calendar button {
  margin: 0;
  border: 0;
  outline: none;
}
.react-calendar button:enabled:hover {
  cursor: pointer;
}
.react-calendar__navigation {
  height: 44px;
  margin-bottom: 1em;
}
.react-calendar__navigation button {
  min-width: 44px;
  background: none;
}
.react-calendar__navigation button:enabled:hover,
.react-calendar__navigation button:enabled:focus {
  background-color: #656565;
}
.react-calendar__navigation button[disabled] {
  background-color: #f0f0f0;
}
.react-calendar__month-view__weekdays {
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 0.9rem;
  color: #d9d9d9;
}
.react-calendar__month-view__weekdays__weekday {
  padding: 0.8rem;
}
.react-calendar__month-view__weekNumbers {
  font-weight: bold;
}
.react-calendar__month-view__weekNumbers .react-calendar__tile {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8em;
  padding: calc(0.75em / 0.75) calc(0.5em / 0.75);
  color: #aeb4b5;
}
.react-calendar__month-view__days__day {
  color: #aeb4b5;
}
.react-calendar__month-view__days__day--weekend {
  color: rgb(130, 18, 48);
}
.react-calendar__month-view__days__day--neighboringMonth {
  color: rgb(134, 188, 194);
}
.react-calendar__year-view .react-calendar__tile,
.react-calendar__decade-view .react-calendar__tile,
.react-calendar__century-view .react-calendar__tile {
  padding: 2em 0.5em;
}
.react-calendar__tile {
  max-width: 100%;
  text-align: center;
  padding: 0.75em 0.5em;
  background: none;
}
.react-calendar__tile:disabled {
  background-color: #f0f0f0;
}
.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
  background-color: #e6e6e6;
}
.react-calendar__tile--now {
  color: #FFFFFF;
  box-shadow: #474747 0px 0px 12px 2px inset;
  border-radius: 8px;
}
.react-calendar__tile--now:enabled:hover,
.react-calendar__tile--now:enabled:focus {
  background: #ffffa9;
}
.react-calendar__tile--hasActive {
  background: #76baff;
}
.react-calendar__tile--hasActive:enabled:hover,
.react-calendar__tile--hasActive:enabled:focus {
  background: #a9d4ff;
}
.react-calendar__tile--active {
  background: #006edc;
  color: white;
}
.react-calendar__tile--active:enabled:hover,
.react-calendar__tile--active:enabled:focus {
  background: #1087ff;
}
.react-calendar--selectRange .react-calendar__tile--hover {
  background-color: #e6e6e6;
}

